import fs from "fs";
import path from "path";

// Blog post type definition
export interface Blog {
  slug: string;
  title: string;
  excerpt: string;
  date: string;
  readTime: string;
  tags: string[];
  content: string;
}

// Blog metadata type (without content)
export interface BlogMetadata {
  slug: string;
  title: string;
  excerpt: string;
  date: string;
  readTime: string;
  tags: string[];
}

// Import individual blog metadata
import { EthernautCTFChallengesWriteupsMetadata } from "./blogs/Ethernaut_CTF_Writeups";
import { gasOptimizationTechniquesMetadata } from "./blogs/gas-optimization-techniques";
import { defiSecurityChecklistMetadata } from "./blogs/defi-security-checklist";

// Utility function to read markdown content
export function getBlogContent(slug: string): string {
  const contentPath = path.join(
    process.cwd(),
    "content",
    "blogs",
    `${slug}.md`
  );
  try {
    return fs.readFileSync(contentPath, "utf-8");
  } catch (error) {
    console.error(`Error reading blog content for slug: ${slug}`, error);
    return "";
  }
}

// Export all blog metadata as an array
export const blogMetadata: BlogMetadata[] = [
  EthernautCTFChallengesWriteupsMetadata,
  gasOptimizationTechniquesMetadata,
  defiSecurityChecklistMetadata,
];

// Export blogs with content (for backward compatibility)
export const blogs: Blog[] = blogMetadata.map((metadata) => ({
  ...metadata,
  content: getBlogContent(metadata.slug),
}));
